*,
::after,
::before {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', 'sans-serif';
}

body {
  margin: 0 auto;
  max-width: 1440px;
}

.App {
  height: 100%;
  padding: 20px;
  display: flex;
  justify-content: center;
  border-left: 1px solid rgb(189, 189, 189);
  border-right: 1px solid rgb(189, 189, 189);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

#root {
  height: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}